import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, ActivityIndicator } from 'react-native';
import { supabase } from './lib/supabase';
import { useEffect, useState } from 'react';
import IncrementButton from './components/IncrementButton';
import { ErrorBoundary } from 'react-error-boundary';

function ErrorFallback({ error }) {
  return (
    <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
      <Text>Something went wrong:</Text>
      <Text>{error.message}</Text>
    </View>
  );
}

export default function App() {
  const [isLoading, setIsLoading] = useState(true);
  const [session, setSession] = useState(null);
  const [error, setError] = useState(null);
  const [networkStatus, setNetworkStatus] = useState('Testing...');

  useEffect(() => {
    console.log('App mounted');
    async function initSupabase() {
      try {
        console.log('Initializing Supabase...');
        const { data: { session }, error } = await supabase.auth.getSession();
        if (error) throw error;

        setSession(session);

        return () => { };
      } catch (e) {
        console.error('Supabase initialization error:', e);
        setError(e.message);
      } finally {
        setIsLoading(false);
      }
    }

    initSupabase();
  }, []);

  useEffect(() => {
    async function checkNetwork() {
      try {
        console.log('Testing Supabase connection...');
        const { data, error } = await supabase
          .from('counters')
          .select('count')
          .limit(1);

        console.log('Supabase test response:', { data, error });

        if (error) {
          setNetworkStatus('‚ùå Supabase Error');
          console.error('Supabase connection error:', error);
        } else {
          setNetworkStatus('‚úÖ Connected');
        }
      } catch (e) {
        console.error('Network test error:', e);
        setNetworkStatus('‚ùå Network Error');
      }
    }

    checkNetwork();
  }, []);

  if (isLoading) {
    return (
      <View style={styles.container}>
        <ActivityIndicator size="large" color="#0000ff" />
      </View>
    );
  }

  return (
    <ErrorBoundary FallbackComponent={ErrorFallback}>
      <View style={[styles.container, { pointerEvents: 'auto' }]}>
        <Text style={styles.title}>React Native + Supabase Demo</Text>
        <IncrementButton />
        <StatusBar style="auto" />
      </View>
    </ErrorBoundary>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
    gap: 20,
  },
  title: {
    fontSize: 20,
    fontWeight: 'bold',
    marginBottom: 20,
  },
  networkStatus: {
    fontSize: 14,
    color: '#666',
    marginBottom: 10,
  },
  status: {
    fontSize: 16,
    color: '#666',
  },
  error: {
    color: 'red',
    fontSize: 16,
  },
});
