import { Button, Text, View, StyleSheet, Platform } from 'react-native';
import { useState, useEffect } from 'react';
import { supabase } from '../lib/supabase';

export default function IncrementButton() {
    const [count, setCount] = useState(0);
    const [error, setError] = useState(null);
    const [connectionStatus, setConnectionStatus] = useState('üîÑ Connecting...');

    useEffect(() => {
        console.log('Initializing channel...');

        // Load initial count
        loadCount();

        const channel = supabase
            .channel('public:counters')
            .on('postgres_changes',
                {
                    event: '*',
                    schema: 'public',
                    table: 'counters'
                },
                (payload) => {
                    console.log('Change received:', payload);
                    if (payload.new && payload.new.count !== undefined) {
                        setCount(payload.new.count);
                    }
                }
            )
            .subscribe((status) => {
                console.log('Subscription status:', status);
                switch (status) {
                    case 'SUBSCRIBED':
                        setConnectionStatus('üü¢ Connected');
                        break;
                    case 'CHANNEL_ERROR':
                        console.log('Channel error details:', channel.state);
                        setConnectionStatus('üî¥ Error');
                        break;
                    case 'CLOSED':
                        setConnectionStatus('‚ö™ Disconnected');
                        break;
                    default:
                        setConnectionStatus('üîÑ Connecting...');
                }
            });

        return () => {
            console.log('Cleaning up channel...');
            supabase.removeChannel(channel);
        };
    }, []);

    async function loadCount() {
        try {
            const { data, error } = await supabase
                .from('counters')
                .select('count')
                .eq('id', 1)
                .single();

            if (error) throw error;
            if (data) {
                console.log('Count:', data.count);
                setCount(data.count);
            }
        } catch (e) {
            console.error('Error loading count:', e);
            setError(e.message);
        }
    }

    async function handleIncrement() {
        try {
            // Optimistic update
            setCount(prevCount => prevCount + 1);
            setError(null);

            const { data, error } = await supabase
                .rpc('increment_counter', {
                    row_id: 1
                });

            if (error) throw error;

            // If the server response doesn't match our optimistic update
            if (data !== count + 1) {
                setCount(data);
            }
        } catch (e) {
            console.error('Error incrementing:', e);
            setError(e.message);
            // Revert optimistic update on error
            loadCount();
        }
    }

    return (
        <View style={styles.container}>
            <Text style={styles.count}>Count: {count}</Text>
            <Text style={styles.status}>{connectionStatus}</Text>
            <Button
                title="Increment"
                onPress={handleIncrement}
            />
            {error && <Text style={styles.error}>{error}</Text>}
        </View>
    );
}

const styles = StyleSheet.create({
    container: {
        alignItems: 'center',
        gap: Platform.OS === 'ios' ? 15 : 10,
    },
    count: {
        fontSize: 24,
        fontWeight: Platform.OS === 'ios' ? '600' : 'bold',
        marginBottom: 10,
    },
    status: {
        fontSize: 12,
        color: '#666',
        marginBottom: 10,
    },
    error: {
        color: 'red',
        fontWeight: 'bold',
        marginTop: 10,
    },
}); 